"use client";

import Link from "next/link";
import Image from "next/image";
import { Session } from "next-auth";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import LogoutButton from "./LogoutButton";
import React, { useState } from "react";

interface HeaderProps {
  session: Session | null;
}

const navLinks = [
  { href: "/", label: "Home" },
  { href: "/books", label: "Books" },
  { href: "/#articles", label: "Articles" },
  { href: "/community", label: "Community" },
];

const Header = ({ session }: HeaderProps) => {
  const [menuOpen, setMenuOpen] = useState(false);
  const [profileDropdownOpen, setProfileDropdownOpen] = useState(false);

  const getUserInitials = (name: string) => {
    return name
      .split(" ")
      .map((n) => n[0])
      .join("")
      .toUpperCase()
      .slice(0, 2);
  };

  return (
    <header className="sticky top-0 z-50 bg-white/90 backdrop-blur border-b border-blue-100 shadow-sm px-4 md:px-8 py-3 flex items-center justify-between">
      <Link href="/" className="flex items-center gap-2">
        <Image src="/icons/logo.svg" alt="logo" width={40} height={40} />
        <span className="font-bold text-blue-900 text-lg hidden sm:inline">NCFP</span>
      </Link>
      {/* Desktop Nav */}
      <nav className="hidden md:flex gap-6 items-center">
        {navLinks.map((link) => (
          <Link key={link.href} href={link.href} className="text-blue-900 font-medium hover:text-blue-700 transition">
            {link.label}
          </Link>
        ))}
        {session?.user ? (
          <div className="relative">
            <button
              className="flex items-center gap-2 text-blue-900 hover:text-blue-700 focus:outline-none"
              onClick={() => setProfileDropdownOpen((v) => !v)}
              onBlur={() => setTimeout(() => setProfileDropdownOpen(false), 150)}
            >
              <Avatar className="h-8 w-8">
                <AvatarImage src="" alt={session.user.name || "User"} />
                <AvatarFallback className="bg-blue-200 text-blue-900 text-sm font-semibold">
                  {session.user.name ? getUserInitials(session.user.name) : "U"}
                </AvatarFallback>
              </Avatar>
              <span className="hidden sm:inline">{session.user.name}</span>
              <svg className="ml-1 w-4 h-4" fill="none" stroke="currentColor" strokeWidth="2" viewBox="0 0 24 24"><path d="M19 9l-7 7-7-7" /></svg>
            </button>
            {profileDropdownOpen && (
              <div className="absolute right-0 mt-2 w-40 bg-white border border-blue-100 rounded shadow-lg z-50">
                <Link href="/my-profile" className="block px-4 py-2 text-blue-900 hover:bg-blue-50">My Profile</Link>
                <div className="border-t border-blue-100" />
                <div className="px-4 py-2">
                  <LogoutButton />
                </div>
              </div>
            )}
          </div>
        ) : (
          <Link href="/sign-in" className="text-blue-900 font-medium hover:text-blue-700 transition">
            Sign In
          </Link>
        )}
      </nav>
      {/* Hamburger for mobile */}
      <button
        className="md:hidden p-2 rounded-full border border-blue-100 bg-white text-blue-900 shadow-sm"
        onClick={() => setMenuOpen((v) => !v)}
        aria-label="Open navigation menu"
      >
        <svg width="24" height="24" fill="none" viewBox="0 0 24 24">
          <path stroke="currentColor" strokeWidth="2" d="M4 6h16M4 12h16M4 18h16" />
        </svg>
      </button>
      {/* Mobile Nav Drawer */}
      {menuOpen && (
        <div className="fixed inset-0 z-40 bg-black/30 flex md:hidden" onClick={() => setMenuOpen(false)}>
          <nav
            className="bg-white w-64 h-full shadow-lg flex flex-col gap-6 p-6 pt-10 relative"
            onClick={(e) => e.stopPropagation()}
          >
            <button
              className="absolute top-4 right-4 p-2 rounded-full hover:bg-blue-50"
              onClick={() => setMenuOpen(false)}
              aria-label="Close navigation menu"
            >
              <svg width="20" height="20" fill="none" viewBox="0 0 24 24">
                <path stroke="currentColor" strokeWidth="2" d="M6 6l12 12M6 18L18 6" />
              </svg>
            </button>
            {navLinks.map((link) => (
              <Link key={link.href} href={link.href} className="text-blue-900 font-medium hover:text-blue-700 transition" onClick={() => setMenuOpen(false)}>
                {link.label}
              </Link>
            ))}
            {session?.user ? (
              <div className="relative">
                <button
                  className="flex items-center gap-2 text-blue-900 hover:text-blue-700 focus:outline-none w-full"
                  onClick={() => setProfileDropdownOpen((v) => !v)}
                  onBlur={() => setTimeout(() => setProfileDropdownOpen(false), 150)}
                >
                  <Avatar className="h-8 w-8">
                    <AvatarImage src="" alt={session.user.name || "User"} />
                    <AvatarFallback className="bg-blue-200 text-blue-900 text-sm font-semibold">
                      {session.user.name ? getUserInitials(session.user.name) : "U"}
                    </AvatarFallback>
                  </Avatar>
                  <span className="hidden sm:inline">{session.user.name}</span>
                  <svg className="ml-1 w-4 h-4" fill="none" stroke="currentColor" strokeWidth="2" viewBox="0 0 24 24"><path d="M19 9l-7 7-7-7" /></svg>
                </button>
                {profileDropdownOpen && (
                  <div className="absolute right-0 mt-2 w-40 bg-white border border-blue-100 rounded shadow-lg z-50">
                    <Link href="/my-profile" className="block px-4 py-2 text-blue-900 hover:bg-blue-50">My Profile</Link>
                    <div className="border-t border-blue-100" />
                    <div className="px-4 py-2">
                      <LogoutButton />
                    </div>
                  </div>
                )}
              </div>
            ) : (
              <Link href="/sign-in" className="text-blue-900 font-medium hover:text-blue-700 transition">
                Sign In
              </Link>
            )}
          </nav>
        </div>
      )}
    </header>
  );
};

export default Header;
